@use "../../css/mixins.scss" as m;

.footer {
  background: #d8f8e3;
  border-radius: 30px 30px 0px 0px;
  width: 100%;
  height: 32vh;
  &__content {
    @include m.width(883px);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}

.menu__title {
  margin: 25px auto 13px auto;
  @include m.font(18px, 25px, unset, #282828);
}

.menu__text {
  margin: 0 auto 10px auto;
  font-style: normal;
  font-weight: 300;
  @include m.font(14px, 19px, unset, #282828);
  &:last-of-type {
    margin-bottom: 0;
  }
  &:hover {
    transition: all ease 0.3s;
    opacity: 0.7;
  }
}

.footer__nav {
  display: flex;
  gap: 44px;
}

.footer__logo {
  @include m.width(80px);
  max-height: 45px;
  margin-top: 10px;
}
.footer__social {
  &__text {
    margin-top: 25px;
    margin-bottom: 10px !important;
    text-align: right;
  }
  &__icons {
    display: flex;
    gap: 10px;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    height: 397px;
    &__logo {
      margin-top: 30px;
    }
    &__content {
      flex-direction: column;
      align-items: center;
    }
    &__menu {
      margin-top: 40px;
      @include m.font(14px, 19px);
      .menu__title {
        margin: 0 auto 20px auto;
      }
      .menu__text {
        @include m.width(130px);
      }
    }
    &__nav {
      gap: 21px;
    }
    &__social {
      margin-top: 40px;
      &__icons {
        gap: 20px;
      }
      &__text {
        margin: 0;
        text-align: center;
      }
    }
  }
}
